@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@600&display=swap);
body{font-family:"Open Sans", sans-serif}.sr-only{position:absolute;left:-10000px;top:auto;width:1px;height:1px;overflow:hidden}

body{margin:0;background-size:cover;min-height:100vh}*{box-sizing:border-box;color:#13342f}ul{list-style-type:none;padding:0}

header{height:100vh;background:#4abdac}.header{width:100vw;position:absolute;padding:20px;top:50%;left:50%;transform:translate(-50%, -50%)}.header__title,.header__subtitle{font-size:3rem;color:white;text-shadow:3px 3px #13342f;font-family:"Poppins", sans-serif;margin:0;text-align:center}.header__subtitle{font-size:2.5rem}.header__image{-webkit-animation:fadeIn 3s forwards;animation:fadeIn 3s forwards;width:auto;height:100%;opacity:0;position:absolute;top:10%}.header__image-wrapper{height:350px;width:350px;border-radius:50%;background:white;overflow:hidden;position:relative;border:3px solid #13342f;margin:35px auto}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}

*{box-sizing:border-box;color:#13342f}.about-section{min-height:101vh;padding:60px;background:#ffffff;position:relative;display:flex;flex-direction:column;justify-content:space-around}.about-section__title{font-size:3rem;margin-top:0}.about-section__container{font-size:20px;max-width:900px;margin:0 auto}.about-section__contact{margin-top:90px}.about-section__logo{height:50px;margin-right:20px}.caps{text-transform:uppercase}

.skills-section{min-height:101vh;display:flex;flex-direction:column;justify-content:space-around;background:#4abdac}.skills-section__container{display:flex;flex-direction:column;background:#ffffff;max-width:900px;min-width:250px;margin:0 auto;text-align:center;border:1px solid #13342f;border-radius:10px;padding:30px;box-shadow:5px 5px 15px #13342f}.skills-section__title{font-size:2rem;margin-top:0;font-family:"Poppins", sans-serif}.skills-section__list{display:flex;flex:2 1;flex-direction:column;justify-content:space-between;margin:0}.skills-section__list li{line-height:1.9rem;font-size:20px}

.recent-work-section{min-height:100vh;background:#4abdac;padding:30px}.recent-work-section__title{font-size:3rem;color:white;text-shadow:3px 3px #13342f;font-family:"Poppins", sans-serif;margin-top:0;text-align:center}.recent-work-section__container{display:flex;margin:30px auto;max-width:1200px;flex-wrap:wrap;justify-content:center}

.project-card{position:relative;display:flex;flex-direction:column;justify-content:space-around;background:#ffffff;padding:30px;height:100%;height:100%;font-size:1rem}.project-card__wrapper{background:#ffffff;height:350px;width:350px;margin:10px;border-radius:10px;overflow:hidden}.project-card__logo{width:100%;height:auto}.project-card__logo--text{font-size:2rem;text-align:center;color:#ffffff;padding:10px}.project-card__title{font-size:20px}.project-card__details{background:#13342f;position:absolute;display:flex;flex-direction:column;justify-content:space-between;height:100%;padding:30px;width:100%;left:0;top:0;text-align:center}.project-card__details *{color:#ffffff;margin:5px 0}.project-card__button{text-decoration:none;color:white;border:1px solid #4abdac;padding:10px;border-radius:50px;justify-self:end}.project-card__button:hover{background:#4abdac;color:white}

